"use client";

import useWindowDimension from "@/hooks/useWindowDimension";
import { useState, useEffect } from "react";
import Image from "next/image";

const Navbar = () => {
  const { scrollY, width } = useWindowDimension();
  const [isScrolled, setIsScrolled] = useState(false);

  useEffect(() => {
    if (scrollY > 50) {
      setIsScrolled(true);
    } else {
      setIsScrolled(false);
    }
  }, [scrollY]);

  return (
    <nav
      className={`z-10 block w-full fixed top-0 transform translate-z-0 transition-all duration-300 ${
        isScrolled ? "bg-white bg-opacity-30 backdrop-blur-md" : ""
      }`}
    >
      <div
        className={`wrap px-[36px] max-md:px-[16px] max-w-[1920px] mx-auto bg-transparent`}
      >
        <div
          className={`flex justify-between min-h-[57px] ${
            isScrolled ? "items-center" : "items-start"
          }  w-full px-0 py-[12px]`}
        >
          <div className="left flex items-center space-x-4 min-h-[30px]">
            <span>
              <div className="flex flex-col justify-between items-center w-5 h-5 bg-transparent border-0">
                <span className="block w-full h-[3px] bg-black"></span>
                <span className="block w-full h-[3px] bg-black"></span>
                <span className="block w-full h-[3px] bg-black"></span>
              </div>
            </span>
            <span className="font-regular-title-bold">MENU</span>
          </div>

          <div className="mid text-center flex-1 relative">
            <Image
              src="/images/brand-title.svg"
              alt="Hero Background"
              width={1200}
              height={600}
              className={`object-cover transition-all duration-300 ${
                isScrolled || (typeof width === "number" && width <= 992)
                  ? "h-[30px] max-w-[130px]"
                  : "h-[90px] max-w-[390px]"
              }  z-0`}
            />
            {/* <h1 className="text-[64px] font-brand-title">LE NOUVEA</h1> */}
          </div>

          <div className="right flex items-center space-x-4 font-regular-title-bold min-h-[30px]">
            {typeof width === "number" && width > 768 && (
              <span>BUY THE ISSUE</span>
            )}
            <span>USA</span>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
